body{
    background-color: #fafbfc;
}

#canvasDiv {
    border: 1px solid var(--gray-color-03);
    border-top-width : 0px;
    /* border-bottom-width : 0px; */
    /* border-right-width : 0px; */
    width: 100%;
    background-color: white;
}

#canvasAllContainer{
    background-color: var(--light-gray-color-02);
    width: calc(100vw - 2px);
    height: calc(100vh - 42px);
    border: 1px solid var(--main-active-color-02);
    /* box-shadow: 2px 2px 10px var(--gray-color-03);
    border-radius: 5px; */
    overflow: hidden;
}

.relative {
position: relative;
}

.flex-center-col {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.flex-start-col {
    display: flex;
    justify-content: start;
    flex-direction: column;
}

.flex-start-row {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
}


.flex-center-row {
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.align-center {
    align-items: center;
    gap:2px;
}

.nonActiveToolBtn {
    background-color: rgba(0, 0, 0, 0);
    
    color: var(--gray-color-02);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}
.nonActiveToolBtn:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}

.activeToolBtn {
    background-color: rgba(123, 104, 196, 0.1);
    color: var(--main-color);
    border: 1px solid rgba(77, 76, 125, 0.3);
    border-radius: 5px;
    padding: 9px;
}

.toolbar {
    padding: 5px;
    width: 40px;
    position: relative;
    background-color: var(--light-gray-color-02);
    border-radius: 0px 0px 0px 5px;
}

.canvasTitleBar{
    /* background-color: var(--main-color-03); */
    background-image: linear-gradient(155deg, var(--main-color-03), #5c5294);
    color: var(--white-color);
    padding: 10px;
    height: 20px;
    display: flex;
}

.brushSizeDiv {
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    left: 55px;
    z-index: 2;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 1px solid var(--gray-color-03);
    color: var(--gray-color-02);
    padding: 4px;
    box-shadow: 2px 2px 5px #aaaaaa;
  }

.brushSizeDiv-free{
    top: 209px;
}

.brushSizeDiv-poly{
    top: 280px;
}

.brushSizeDiv-curve{
    top: 317px;
}

.closebrushSizeBtn{
    color: var(--black-color);
    background-color: rgba(255, 255, 255, 0);
    border: none;
}

.closebrushSizeBtn:hover {
    cursor: pointer;
    color: var(--gray-color);
}


/* 컬러 프리뷰 구역 */

.PreviewColorSquare{
    width: 30px;
    height: 30px;
    border: 1px solid #555555;
}



.mainColorDiv {
    position: relative;
    z-index: 3;
}

.subColorDiv {
    position: relative;
    bottom: 20px;
    left: 8px;
    z-index: 1;
}

.PreviewColors{
    position: relative;
    margin-top: 3px;
    height: 45px;
    width : 40px;
}

.changeMainSubColorBtn{
    position:absolute;
    top: -4px;
    z-index:3;
    left: 32px;
    color : var(--gray-color);
}

.changeMainSubColorBtn:hover{
    color: var(--main-color);
    cursor: pointer;
}

.changeMainSubColorIcon{
    width: 13px;
    height: 13px;
}


