/* 다운로드 */
.downloadDiv{
    display: flex;
    align-items: center;
    margin: 0px 2px;
    margin-bottom: 2px;
    gap: 5px;
}


/*폰트설정*/


.fontSettingsBox {

    position: absolute;
    /* left: 50px;
    top: 580px; */

    width: 300px;
    height: 50px;
    z-index: 3;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 1px solid var(--gray-color-02);
    padding: 5px;
    box-shadow: 2px 2px 5px #aaaaaa;
  }


  .fontFamilySelbox{
    width: 170px;
    height: 25px;
  }

  .fontsize{
    width: 50px;
    height: 19px;
  }

  .fontIcon{
    width: 18px;
    height: 18px;
  }

.nonActiveSettingBtn {
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}
.nonActiveSettingBtn:hover {
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}

.activeSettingBtn {
    width: 30px;
    height: 30px;
    /* background-color: rgba(68, 0, 194, 0.1); */
    color: var(--main-color);
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 2px;
}

.dividerIcon{
    width: 25px;
    height: 25px;
    color: var(--gray-color);
}



/* 상단바 */
.topbar{
    background-color: var(--light-gray-color-02);
    border: 1px solid var(--gray-color-03);
    border-top-width : 0px;
    border-left-width : 0px;
    border-right-width : 0px;
    padding: 2px;
    padding-left:5px;
    display: flex;
    align-items: center;
    gap:5px;
    color: var(--gray-color-02);
}

.titlebar{
    /* background-color: var(--main-color-02); */
    background-image: linear-gradient(115deg, var(--main-color-02), #a595bb);
    /* border-radius: 4px 4px 0px 0px; */
}
.projectName{
    margin: 8px 0px 8px 8px;
    font-size: 18px;
    color: var(--white-color);
}

hr {
    background:lightgray;
    height:1px;
    border:0;
}

.updown {
    background-color: lightgray;
    width: 0.1px;
    height: 35px;
}

.nonActiveTopBtn {
    background-color: rgba(0, 0, 0, 0);
    
    color: var(--gray-color-02);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 5px;
}
.nonActiveTopBtn:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 5px;
}

.nonActiveTopBtn:active {
    color: var(--main-color);
}

.topgroup{
    margin: 0px 2px;
    display: flex;
    align-items: center;
}

.titlebarbtn{
    margin: 0px 10px 0px 3px;
    color: rgba(256, 256, 256, 0.7);
}

.titlebarbtn:hover{
    color: var(--black-color);
    cursor: pointer;
}

.exportBtn{
    background-color: #f7f8f9;
    color: var(--gray-color-02);
    border: 1px solid var(--gray-color-03);
    border-radius: 8px;
    padding: 5px 8px;
}

.exportBtn:hover{
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    cursor: pointer;
}
