
.customCmpnt{
    position: fixed;
    left: 50%;
    transform: translateX( -50% );
    top: 30px;
}

.customAlert{
    background-color: var(--main-color-03);
    color: var(--main-color-04);
    padding: 15px 20px;
    border-radius: 30px;
    box-shadow: 0px 1px 8px var(--main-color);
    z-index: 2;
}

.customConfirm{
    background-color: var(--main-color-04);
    color: var(--main-color-03);
    padding: 12px 18px;
    max-width: 420px;
    border-radius: 10px;
    box-shadow: 0px 1px 10px var(--main-color-02);
    z-index: 2;
}

.customPrompt{
    background-color: var(--main-color-04);
    color: var(--main-color-03);
    padding: 15px 20px;
    border-radius: 10px;
    box-shadow: 0px 1px 10px var(--main-color-02);
    z-index: 2;
}

.customCmpntBtns{
display: flex;    
}

.promptMsg{
    font-weight: bold;
    font-size: 13pt;
    padding: 5px 0px 15px 0px;
}

.confirmMsg{
    font-weight: bold;
    font-size: 12pt;
    padding: 10px 5px 10px 0px;
}

.inputbox {
    border: solid 1px #eaebec;
	border-radius: 5px;
    padding: 10px 10px;
    width: 300px;
    color: var(--main-color-02);
    font-size: 12pt;
    margin-bottom: 5px;

}
.inputbox:focus-within {outline: solid 1px var(--main-color-04);}

.confirmBtn {
    background-color: var(--main-color-02);
    color: var(--white-color);
    border:none;
	border-radius: 5px;
    width: 50px;
    height: 30px;
    margin: 2px;
}

.confirmBtn:hover {
    cursor: pointer;
    background-color: var(--main-active-color-02);
    border: 1px solid var(--main-active-color-02);
}

.cancelBtn {
    background-color: var(--white-color);
    color: var(--main-color-02);
    border:none;
	border-radius: 5px;
    width: 50px;
    height: 30px;
    margin: 2px;

    
}

.cancelBtn:hover {
    cursor: pointer;
    background-color: var(--light-gray-color);
    border: 1px solid var(--light-gray-color-02);
}


.transparencyBackground{
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    z-index: 2;
}