.infoPage{
    display: flex;
}

.infoCategory{
    width: 200px;
    height: calc(100vh - 70px);  /* 타이틀바 높이 40px, padding 세로값 30px 빼기*/
    padding: 15px;
    background-color: var(--light-gray-color);
}

.infoBtn{
    width: 178px;
    /* border: 1px solid var(--main-color); */
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 5px;
    color: var(--main-color-02);
    font-weight: bold;
}

.infoBtn:hover{
    /* border: 1px solid var(--main-color-02); */
    box-shadow: 0px 0px 5px var(--main-color);
    border-radius: 10px;
    color: var(--main-color);
    cursor: pointer;
}


.infoContent{
    padding: 40px;
    height: calc(100vh - 120px);  /* 타이틀바 높이 40px, padding 세로값 30px 빼기*/
    width: calc(100vw - 200px);
    overflow:auto;
}